body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  min-height: 100%;
}

.textBox {
  font-family: "Abel", sans-serif;
  background-color: rgba(255, 255, 255, 0.603);
  border-radius: 5px;
  box-shadow: 6px 6px 15px rgb(32, 32, 32);
  position: absolute;
  bottom: 50px;
  left: 50px;
  width: 300px;
  padding: 20px;
  text-align: center;
  color: #122f5e;
  font-size: 22px;
}

.carouselItemDiv {
  width: 100%;
  position: relative;
}

.carouselItemTitle {
  font-weight: bold;
  font-size: 24px;
}

.carouselItemDescription {
  font-size: 18px;
}

.footer {
  width: 100%;
  height: 100px;
  background-color: #122f5e;
  color: white;
  font-family: "Abel", sans-serif;
}

.row {
  text-align: center;
}

.col {
  margin-top: 45px;
}

.eventDiv {
  background: rgba(255, 255, 255, 0.493);

  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.281);
  border-radius: 3px;
  margin: 15px 10px;
  text-align: center;
  padding: 20px 10px 5px 10px;
}

.eventDiv:hover {
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.534);
}

.eventTitle {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
}

.react-calendar {
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.247);
  border-radius: 3px;
  border: none;
}

.react-calendar__tile {
  border-radius: 3px;
}

.react-calendar__month-view__days__day--weekend {
  color: #fe7f18;
}

.react-calendar__tile--now {
  background-color: #122f5e;
  color: white;
}

.mainPage {
  width: 100%;
  height: 100%;
}

.eventPageDiv {
  font-family: "Abel", sans-serif;
  padding: 50px 90px;
  background-color: rgb(221, 221, 221);
  color: #122f5e;
}

.lineDivider {
  margin-bottom: 40px;
}

.eventList {
  text-align: center;
}

.addEventBtn {
  height: auto !important;
  height: initial !important;
  margin-bottom: 10px;
  border-radius: 3px;
  background-color: #fe7f18;
  border: none;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.281);
  font-family: "Abel", sans-serif;
}

.addEventBtn:hover {
  background-color: #122f5e;
}

.calendarDiv {
  text-align: center;
}

.titleInput,
.descriptionInput {
  width: 95%;
  margin: 5px;
  border-radius: 3px;
  padding: 5px;
  border: 1px solid rgb(202, 202, 202);
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.075);
}

.submitEventBtn {
  margin-bottom: 15px;
  height: auto;
  height: initial;
  margin-bottom: 10px;
  border-radius: 3px;
  color: white;
  background-color: #122f5e;
  border: none;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.281);
}

.submitEventBtn:hover {
  background-color: #fe7f18;
}

/* .createEventDiv {
  display: none;
} */

label {
  margin: 15px 3px 3px 0px;
  font-weight: bold;
}

.navbar {
  width: 100%;
  height: 50px;
  background: linear-gradient(#122f5e, #5e779e);
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.425);
  z-index: 999;
  padding: 0px;
  overflow: hidden;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.sticky {
  position: fixed;
  z-index: 999;
}

.btn {
  border-radius: 0px;
  height: 100%;
}

.loginBtn,
.registerBtn,
.loginBtnDiv {
  height: 100%;
}

.registerBtn {
  background-color: #30363b;
}

.registerBtn:hover {
  background-color: #222629;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.7);
}

.loginBtn:hover,
.donateBtn:hover {
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.7);
}

.loginBtn,
.registerBtn,
.donateBtn {
  font-family: "Abel", sans-serif;
  font-weight: bold;
  font-size: 20px;
  border: none;
}

.donateBtn {
  font-size: 20px;
  background-color: #fe7f18;
}

.donateBtn:hover {
  background-color: #ec6a00;
}

/* lower nav section */
.lowerNav {
  background-color: white;
  text-align: center;
}

.logo {
  padding-top: 20px;
  height: 150px;
}

.alumniText {
  font-size: 22px;
  color: #024885;
  font-family: "Times New Roman", Times, serif;
}

:root {
  --mainColor: #f76f00;
  --accentColor: black;
}

.navLink {
  font-family: "Abel", sans-serif;
  font-weight: bold;
  font-size: 18px;
  border-radius: 3px;
  text-transform: uppercase;
  text-decoration: none;
  color: #122f5e;
  padding: 7px 2px;
  background: linear-gradient(
    to bottom,
    #f76f00 0%,
    #f76f00 100%
  );
  background: linear-gradient(
    to bottom,
    var(--mainColor) 0%,
    var(--mainColor) 100%
  );
  background-position: 0 100%;
  background-repeat: repeat-x;
  background-size: 4px 4px;
  text-decoration: none;
  transition: background-size 0.3s;
}

.navLink:hover {
  text-decoration: none;
  color: white;
  background-size: 4px 50px;
}

.fa-caret-right {
  font-size: 20px;
}

.navItemDiv {
  margin: 30px;
}

.navRow {
  line-height: 0.5px;
  justify-content: center;
}

